.dialog {
  font-size: 18px;
  margin-bottom: 22px;
}
.dialog.active {
  color: #2196f3;
}
.dialog_avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 12px;
}
.dialog_avatar img {
  max-width: 100%;
  height: 100%;
}
.dialog_link {
  display: flex;
  align-items: center;
}